@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

html, body {
    max-width: 100%;
}

body{
    font-family: 'Poppins', sans-serif;
    overflow: auto;
}
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}


.side-bar {
    background: white;
    min-height: 100vh;
    max-height: 100vh;
}

.side-bar a {
    color: black;
    background-color: transparent;
    text-decoration: none;
 }

 .header .logo{
    height: 75px;
    padding-right: 40px;
 }
 .logo img {
     width: 180px;
     height: 100px;
 }
 .navbar-sidebar {
     padding: 35px;
     border: 1px solid #e6eaee;
     border-radius: 5px;
 }
 .navbar-sidebar li {
    padding: 10px;
    border-bottom: 1px solid #e6eaee;
}
.content{
    background: #edeff5;
    height: 100%;
}
.no-padding{
    padding-right: 0 !important;
    padding-left : 0 !important;
}
.main-content {
    padding: 40px;
}
.fa, .fas {
    font-weight: 900;
    padding-right: 7px;
}

.box{
    border-radius: 18px;
    background-image: -webkit-linear-gradient(90deg, #3f5efb 0%, #fc466b 100%);
}
.most-visted {
    background-image: -webkit-linear-gradient(90deg, #11998e 0%, #38ef7d 100%);
}
.revenue {
    background-image: -webkit-linear-gradient(90deg, #ee0979 0%, #ff6a00 100%);
}
.weelely {
    background-image: -webkit-linear-gradient(90deg, #45b649 0%, #dce35b 100%);
}
.overview_inner{
    padding: 20px;
    text-align: center;
}
.overview_inner .text{
    display: inline-block;
    color: white;
    text-align: center;
    padding-left: 25px;
}
.overview_inner .icon{
    display: inline-block;
    color: white;
    font-size: 40px;

}
.canvas{
    text-align: center;
}

.header{
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    background: #ffffff;
    box-shadow: 0 1px 15px 1px rgba(62,57,107,.1);
    border-bottom: 1px solid #e2ebf7;
}

.search-bar {
    margin: 0px 0 0 45px;
    display: flex;
     align-items: baseline;
}
.form-input {
    line-height: 43px;
    border: 1px solid #e5e5e5;
    font-size: 14px;
    color: #666;
    padding: 0 10px;
    border-radius: 3px;
    width: 300px;
}
.search-icon{
    background: #4272d7;
    color: white;
    padding: 9px 19px;
    margin: -4px;
}
.user-notfication {
    display: flex;
    justify-content: space-between;
    margin-right: 50px;
    direction: rtl;
    align-items: center;
}
ul.user-notfication li i {
    padding: 20px;
    font-size: 26px;
    color: gray;
}
.user-image img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-image: -webkit-linear-gradient(90deg, #11998e 0%, #38ef7d 100%);
}
.user-name {
    font-weight: 700;
    padding-left: 13px;
}
ul.notification-icons {
    display: flex;
}
ul.notification-icons > li {
    padding-right: 25px;
}
table th {
    width:  150px;
    height: 10px;
    border: 1px solid #ddd;
}
table tr:nth-of-type(even) {
    background-color: #f5f5f5;
}
table th , tr , td {
    border-collapse: collapse;
    border: 1px solid #ddd;
    vertical-align: baseline;
    padding: 8px;

}
table {
    border-collapse: collapse;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    overflow: hidden;
    width: 100%;
    background: white;
}
.data-section{
    margin-top: 20px;
    padding: 20px;
    background: white;
}
.top-countries {
    background: #2949fe;
    padding: 30px;
    border-radius: 10px;
}
.top-countries ul li{
    display: flex;
    align-items: baseline;
    border-bottom: 1px solid;
    text-align: center;
    color: white;
    justify-content: space-between;
    padding: 10px;
}
.top-countries ul li h6{
    font-size: 14px;
}
form {
    display: flex;
    flex-direction: column;
}
form input {
    display: inline-block;
}



.product-page {
    /* background: #edeff5; */
    /* padding: 60px; */
    padding-top: 25px;
}
.productImage p {
    display: inline;
    padding-left: 10px;
}
.productImage {
    padding: 0;
    /* width: 100%; */
}
.teacher {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #00000024;
}
.teacher-details {
    flex: .9;
}

.rating {
    display: inline;
}

li.teacher img {
    border-radius: 6px;
    width: 100px;
    height: 100px;
}
.teacher .tilte {
    font-weight: 700;
}
a.button {
    float: right;
    clear: both;
    background: green;
    color: white;
    padding: 10px;
    border-radius: 3px;
    cursor: pointer;
}
a.button:hover {
    color: white;
    text-decoration: none;
  }


h5.price {
    float: right;
}
ul.all-teachers {
    padding: 10px;
}

.checkbox-btn {
    direction: rtl;
}
label.checkbox-btn input {
    margin: 10px;
    vertical-align: middle;
    height: 15px;
    width: 15px;
}
.streamer {
    display: block;
    border: 2px solid black;
}

.userProfile {
    padding: 50px;
}
.userProfile .title {
    padding: 20px;
}



@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;	
	font-family: Raleway, sans-serif;
}

body {
	background: linear-gradient(90deg, #C7C5F4, #776BCC);		
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}

.screen {		
	background: linear-gradient(90deg, #5D54A4, #7C78B8);		
	position: relative;	
	height: 600px;
	width: 360px;	
	box-shadow: 0px 0px 24px #5C5696;
}

.screen__content {
	z-index: 1;
	position: relative;	
	height: 100%;
}

.screen__background {		
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	-webkit-clip-path: inset(0 0 0 0);
	clip-path: inset(0 0 0 0);	
}

.screen__background__shape {
	transform: rotate(45deg);
	position: absolute;
}

.screen__background__shape1 {
	height: 520px;
	width: 520px;
	background: #FFF;	
	top: -50px;
	right: 120px;	
	border-radius: 0 72px 0 0;
}

.screen__background__shape2 {
	height: 220px;
	width: 220px;
	background: #6C63AC;	
	top: -172px;
	right: 0;	
	border-radius: 32px;
}

.screen__background__shape3 {
	height: 540px;
	width: 190px;
	background: linear-gradient(270deg, #5D54A4, #6A679E);
	top: -24px;
	right: 0;	
	border-radius: 32px;
}

.screen__background__shape4 {
	height: 400px;
	width: 200px;
	background: #7E7BB9;	
	top: 420px;
	right: 50px;	
	border-radius: 60px;
}

.login {
	width: 320px;
	padding: 30px;
	padding-top: 156px;
}

.login__field {
	padding: 20px 0px;	
	position: relative;	
}

.login__icon {
	position: absolute;
	top: 30px;
	color: #7875B5;
}

.login__input {
	border: none;
	border-bottom: 2px solid #D1D1D4;
	background: none;
	padding: 10px;
	padding-left: 24px;
	font-weight: 700;
	width: 75%;
	transition: .2s;
}

.login__input:active,
.login__input:focus,
.login__input:hover {
	outline: none;
	border-bottom-color: #6A679E;
}

.login__submit {
	background: #fff;
	font-size: 14px;
	margin-top: 30px;
	padding: 16px 20px;
	border-radius: 26px;
	border: 1px solid #D4D3E8;
	text-transform: uppercase;
	font-weight: 700;
	display: flex;
	align-items: center;
	width: 100%;
	color: #4C489D;
	box-shadow: 0px 2px 2px #5C5696;
	cursor: pointer;
	transition: .2s;
}

.login__submit:active,
.login__submit:focus,
.login__submit:hover {
	border-color: #6A679E;
	outline: none;
}

.button__icon {
	font-size: 24px;
	margin-left: auto;
	color: #7875B5;
}

.social-login {	
	position: absolute;
	height: 140px;
	width: 160px;
	text-align: center;
	bottom: 0px;
	right: 0px;
	color: #fff;
}

.social-icons {
	display: flex;
	align-items: center;
	justify-content: center;
}

.social-login__icon {
	padding: 20px 10px;
	color: #fff;
	text-decoration: none;	
	text-shadow: 0px 0px 8px #7875B5;
}

.social-login__icon:hover {
	transform: scale(1.5);	
}